.popup__title {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 0 0 50px 0;
}

.popup__title-infoTooltip {
    text-align: center;
    margin-bottom: 23px;
}

@media screen and (max-width: 600px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 75px;
    }

    .popup__title-infoTooltip {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 20px;
    }
}

