.login__button {
    width: 100%;
    height: 50px;
    background-color: #fff;
    color: black;
    border-radius: 2px;
    margin-top: 186px;
    cursor: pointer;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    transition: opacity .5s linear;
    border: none;
  }

  .login__button:hover{
    opacity: .8;
  }
  
  .login__button_inactive {
    background-color: rgb(107, 105, 105);
    color: rgb(154, 147, 147);
    
  }
  
  @media screen and (max-width: 528px) {
    .login__button {
      margin-top: 138px;
      font-size: 16px;
      line-height: 19px;
    }
  }